import React, { Component } from 'react';
import { get, has } from 'lodash'
import { Link } from "react-router-dom";

import { addResizeListenerIsMobile, isMobile } from "../shared/services/responsive.service"

import styles from './iframe.module.scss'
const title = "Browser"

class Iframe extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isMobile: isMobile()
    }
  }

  componentDidMount() {
    addResizeListenerIsMobile(this, "isMobile")
  }

  render() {
    const { isMobile } = this.state
    const pathSimple = has(this, ["props", "match", "params", "path"])
    const path = get(this, ["props", "match", "params", "path"], get(this, ["props", "match", "params", "pathwhitepaper"]), "")

    return <div className={!isMobile ? styles.iframeBlock : styles.iframeMobileBlock}>
      <Link to={pathSimple ? "/" : "/games/whitepaper"} className={styles.closeButton} />
      <div className={styles.contentBlock}>
        <iframe className={styles.contentIframe} src={decodeURIComponent(path)}>
          Your system doesn't support my Browser... go link please

          <a href={decodeURIComponent(path)} target="_blank">{decodeURIComponent(path)}</a>
        </iframe>
      </div>

      <span className={styles.address}><a href={decodeURIComponent(path)} target="_blank">{decodeURIComponent(path)}</a></span>
      <h1 className={styles.title}>{title}</h1>
    </div>
  }
}

export default Iframe